 *
 * @author fqq
 */
import { Vue, Component, Prop, Model, Watch } from "vue-property-decorator";

@Component({
  components: {}
})
export default class AddSubSystem extends Vue {
  @Prop({ type: String, default: "添加子系统" })
  dialogTitle?: string;

  @Prop({ type: Boolean, default: false })
  dialogVisible!: boolean;

  @Prop({ type: Object, default: () => ({}) })
  data!: any;

  @Watch("dialogVisible")
  handleVisible(newValue: boolean) {
    this.addDialog = newValue;
    if (newValue) {
      this.$nextTick(() => {
        let ref: any = this.$refs.addForm;
        ref.resetFields();
        this.addForm = JSON.parse(JSON.stringify(this.data));
      });
    }
  }

  addDialog = false;
  addForm: any = {
    id: "",
    subsystemName: "",
    code: "",
    subsystemVersion: ""
  };
  formRules: any = {
    subsystemName: {
      required: true,
      message: "该选项为必填项",
      trigger: "change"
    },
    code: {
      required: true,
      message: "该选项为必填项",
      trigger: "change"
    },
    subsystemVersion: {
      required: true,
      message: "该选项为必填项",
      trigger: "change"
    }
  };

  closeDialog() {
    this.$emit("close");
  }

  saveSensorAttribute() {
    let rest: any = this.$refs.addForm;
    rest.validate((valid: any) => {
      if (valid) {
        this.$emit("submit", this.addForm);
      } else {
        return false;
      }
    });
  }
}
