
































import { Vue, Component, Emit, Watch } from "vue-property-decorator";
import AddSubSystem from "./components/AddSubSystem.vue";

import Url from "../../api";
import utils from "@/utils/utils";

@Component({
  components: {
    AddSubSystem
  }
})
export default class Subsystem extends Vue {
  loading: boolean = false;
  tableData: Array<any> = [];

  // 添加弹窗
  dialogTitle: string = "";
  visibleDialog: boolean = false;
  formData: any = {
    id: "",
    subsystemName: "",
    code: "",
    subsystemVersion: ""
  };

  created() {
    this.getTableData();
  }

  // 获取表格数据
  async getTableData() {
    this.loading = true;
    try {
      let {
        data: { data, status, message }
      } = await this.$http.get(Url.getSubsystemList);
      if (status) {
        this.tableData = data || [];
      } else {
        this.$message.error(message);
      }
    } finally {
      this.loading = false;
    }
  }

  // 添加
  handleAdd() {
    this.dialogTitle = "添加子系统";
    this.formData = {
      id: "",
      subsystemName: "",
      code: "",
      subsystemVersion: ""
    };
    this.visibleDialog = true;
  }

  // 修改
  handleEdit(item: any) {
    this.dialogTitle = "修改子系统";
    utils.mapValue(this.formData, item);
    this.visibleDialog = true;
  }

  // 删除
  handleDelete(item: any) {
    let _this = this;
    this.$confirm("确认删除？", "提示", {
      confirmButtonText: "确定",
      cancelButtonText: "取消",
      type: "warning"
    })
      .then(() => {
        this.$http.get(Url.deleteSubsystem + "/" + item.id).then((res: any) => {
          if (res.status) {
            if (res.data.status) {
              this.$message({
                type: "success",
                message: "删除成功!"
              });
              //重新获取列表
              _this.getTableData();
            } else {
              _this.$message({
                showClose: true,
                message: res.data.message,
                type: "error"
              });
            }
          }
        });
      })
      .catch(() => {});
  }

  closeDialog() {
    this.visibleDialog = false;
  }

  async saveSubsystem(form: any) {
    try {
      let {
        data: { data, status, message }
      } = await this.$http.post(form.id ? Url.updateSubsystem : Url.addSubsystem, form);
      if (status) {
        this.$message.success(form.id ? "修改成功" : "添加成功");
        this.visibleDialog = false;
        this.getTableData();
      } else {
        this.$message.error(message);
      }
    } finally {
      //
    }
  }
}
